.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-title {
    font-size: 1.5em;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.search-container {
    margin-top: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.searchbox {
    border-radius: 5px;
    border: 1px solid #ccc;
    padding: 10px;
    width: 350px;
    height: 20px;
}

.result-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.tbl-content {
  width: auto;
  margin-bottom: 50px;
}

table,
th,
td {
    border: 1px solid #ccc;
    border-collapse: collapse;
    padding: 5px;
}

th {
    text-align: left;
}

.searchinfo {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

ul {
    list-style: none;
}

ul li {
    float: left;
    width: 20px;
    margin: 10px;
}

ul li a {
    text-decoration: none
}

.page-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
a.pagelink{

  color:blue;
  cursor: pointer;
}

a.pagelink:hover{

  color: hotpink
}

a.pagelink:visited{
  text-decoration: none;
  color: hotpink
}


